export default{
    data(){
        return {
            //
            share:{
                title:'',
                path:'/pages/index/index',
                imageUrl:'',
                desc:'',
                content:''
            }
        }
    },
    onShareAppMessage(res) {
        return {
            title:this.share.title,
            path:this.share.path,
            imageUrl:this.share.imageUrl,
            desc:this.share.desc,
            content:this.share.content,
            success(res){
                uni.showToast({
                    title:''
                })
            },
            fail(res){
                uni.showToast({
                    title:'',
                    icon:'none'
                })
            }
        }
    },
	onShareTimeline(e){
		return {
		    title:this.share.title,
		    path:this.share.path,
		    imageUrl:this.share.imageUrl,
		    desc:this.share.desc,
		    content:this.share.content,
		    success(res){
		        uni.showToast({
		            title:''
		        })
		    },
		    fail(res){
		        uni.showToast({
		            title:'',
		            icon:'none'
		        })
		    }
		}
	}
}